.header {
	padding: 50px 0 130px;
	&__phones,
	&__social {
		padding: 39px 0 42px;
		@include sm {
			padding: 35px 0; }
		@include xs {
			padding-bottom: 0; } }
	&__main {
		overflow: hidden; }
	&__inner {
		@include md {
			overflow: hidden; }
		@include sm {
			overflow: visible; } }
	&__logo {
		position: relative;
		padding: 5px 0;
		margin: 0 auto;
		max-width: 360px;
		font-size: 0;
		text-align: center;
		@include md {
			padding-top: 40px;
			max-width: 216px; }
		@include xs {
			padding-top: 0;
			clear: both; }
		&:before,
		&:after {
			content: "";
			position: absolute;
			top: 110px;
			margin: 0 20px;
			width: 3000px;
			border-bottom: 2px dashed $white;
			opacity: 0.2;
			@include xs {
				top: 70px; } }
		&:before {
			right: 100%; }
		&:after {
			left: 100%; }
		&-circle {
			position: absolute;
			top: 60px;
			left: 0;
			right: 0;
			overflow: hidden;
			height: 100px;
			transform: translate3d(0,0,0);
			@include md {
				top: 80px;
				height: 60px; }
			@include xs {
				top: 40px; }
			&:before {
				content: "";
				position: absolute;
				top: 50%;
				left: 0;
				right: 0;
				border: 2px solid $white;
				border-radius: 50%;
				height: 360px;
				opacity: 0.09;
				transform: translate3d(0,-50%,0);
				@include md {
					height: 216px; } } } } }

.logo {
	display: inline-block;
	vertical-align: top;
	width: 170px;
	@include md {
		width: 115px; }
	img {
		width: 100%; } }


.btn-nav {
	position: relative;
	display: none;
	vertical-align: top;
	padding: 5px 10px 5px 40px;
	background: none;
	@include sm {
		display: inline-block; }
	@include hover {
		.btn-nav {
			&__icon {
				&:before,
				&:after,
				i {
					border-color: $yellow; }
				&:before {
					transform: translate3d(0, -3px, 0); }
				&:after {
					transform: translate3d(0, 3px, 0); } }
			&__text {
				color: $yellow; } } }
	&.is-active {
		@include hover {
			.btn-nav {
				&__icon {
					&:before {
						transform: translate3d(4px, -3px, 0) rotate(45deg); }
					&:after {
						transform: translate3d(4px, 3px, 0) rotate(-45deg); } } } }
		.btn-nav {
			&__icon {
				&:before {
					transform: translate3d(4px, -3px, 0) rotate(45deg); }
				&:after {
					transform: translate3d(4px, 3px, 0) rotate(-45deg); }
				i {
					transform: scaleX(0); } } } }
	&__icon {
		position: absolute;
		top: 16px;
		left: 0;
		height: 15px;
		&:before,
		&:after,
		i {
			position: absolute;
			left: 0;
			width: 28px;
			border-bottom: 1px solid $white;
			transition: transform 0.175s ease, border-color 0.25s ease; }
		&:before,
		&:after {
			content: "";
			@include gpu; }
		&:before {
			top: 0;
			transform-origin: top left; }
		&:after {
			bottom: 0;
			transform-origin: bottom left; }
		i {
			top: 7px; } }
	&__text {
		display: block;
		color: $white;
		@include f('lt', 18, 40);
		text-transform: uppercase;
		@include t(color); } }

.menu {
	position: fixed;
	z-index: 1000;
	top: 0;
	left: 0;
	right: 0;

	color: $white;
	background: $gray;

	@include f('ll', 18, 40);
	text-transform: uppercase;
	a {
		color: inherit; }
	&__link {
		float: right;
		display: none;
		padding: 5px 0;
		margin-right: 140px;
		@include t(color);
		@include sm {
			display: block; }
		@include xs {
			margin-right: 0; }
		@include hover {
			color: $yellow; } } }

.nav {
	@include sm {
		position: fixed;
		top: 50px;
		left: 0;
		bottom: 0;
		padding: 30px 0 20px;
		width: 90%;
		max-width: 300px;
		background: $gray;
		transform: translate3d(-100%, 0, 0);
		transition: transform 0.25s ease; }
	&.is-active {
		@include sm {
			@include gpu; } }
	&__list {
		font-size: 0.1px;
		text-align: justify;
		&:after {
			content: "";
			display: inline-block;
			width: 100%; }
		li,
		a {
			display: inline-block;
			vertical-align: top;
			@include sm {
				display: block;
				padding: 0 20px; } }
		a {
			padding: 5px 0;
			font-size: 18px;
			@include t(color);
			@include hover {
				color: $yellow; } } } }

.social {
	float: right;
	font-size: 0;
	&__mail {
		float: left;
		@include f('ll', 18, 30);
		text-decoration: underline;
		@include t(color);
		@include md {
			@include f(null, 16, 24); }
		@include sm {
			margin: 12px 0; }
		@include xs {
			@include f(null, 14, 24); }
		@include hover {
			color: $yellow; } }
	&__item,
	&__link {
		display: inline-block;
		vertical-align: top; }
	&__list {
		float: right;
		padding-left: 14px;
		@include sm {
			display: none; } }
	&__item {
		padding-left: 10px; }
	&__link {
		border-radius: 50%;
		width: 30px;
		height: 30px;
		opacity: 0.25;
		background: $white;
		@include vertical;
		transition: background 0.2s ease, opacity 0.2s ease;
		@include hover {
			opacity: 1;
			.icon {
				fill: $white; } }
		&_fb {
			@include hover {
				background: #38569a; }
			.icon {
				font-size: 19px; } }
		&_vk {
			@include hover {
				background: #41658b; }
			.icon {
				font-size: 14px; } }
		&_inst {
			@include hover {
				background: #3d6b92; }
			.icon {
				font-size: 17px; } }
		.icon {
			display: inline-block;
			vertical-align: middle;
			fill: $black;
			transition: fill 0.2s ease; } } }

.phones {
	float: left;
	font-size: 0;
	&__item,
	&__link {
		display: inline-block;
		vertical-align: top;
		@include sm {
			display: block; } }
	&__item {
		padding-right: 5px;
		&:not(:last-child) {
			&:after {
				content: ",";
				display: inline-block;
				vertical-align: top;
				@include f('ll', 18, 30);
				@include sm {
					display: none; } } } }
	&__link {
		@include f('ll', 18, 30);
		@include t(color);
		@include md {
			@include f(null, 16, 24); }
		@include xs {
			@include f(null, 14, 24); }
		@include hover {
			color: $yellow; } } }
